        rgba(74, 144, 226, 0.6)
    );
}

/* Add arrow indicator */
.dropdown-toggle::after {
    content: '';
    margin-left: 2px;
    font-size: 0.8em;
    display: none; /* Hide by default */
}

.dropdown:hover .dropdown-toggle::after {
    transform: rotate(180deg);
}

/* Animation for dropdown items */
@keyframes fadeInRight {
    to {
