/* Create a grid container for job cards */
.jobs-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns */
  grid-gap: 30px;
  gap: 30px; /* Space between cards */
  margin-top: 40px;
  perspective: 2000px; /* Add perspective for 3D effect */
}

.job-card {
  background: white;
  border-radius: 10px;
  padding: 25px;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  transform-style: preserve-3d;
  transform: translateZ(0);
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  will-change: transform, box-shadow;
}

.jobs-grid .job-card:hover {
  transform: translateZ(50px) !important;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.2),
    0 15px 20px rgba(0, 0, 0, 0.1) !important;
}

/* Add subtle animation to content on hover */
.job-card > * {
  transition: transform 0.3s ease;
}

.job-card:hover > * {
  transform: translateZ(10px); /* Push content slightly forward */
}

/* Responsive design */
@media screen and (max-width: 768px) {
  .jobs-grid {
    grid-template-columns: 1fr; /* Single column on mobile */
