.phase-card:hover {
  transform: translateY(-5px);
  border-color: rgba(74, 144, 226, 0.4);
  box-shadow: 0 5px 15px rgba(74, 144, 226, 0.15);
}

.phase-number {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: #4a90e2;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.2rem;
  box-shadow: 0 2px 10px rgba(74, 144, 226, 0.3);
}

.phase-icon {
  font-size: 2rem;
  color: #4a90e2;
  margin: 1.5rem 0;
  transition: transform 0.3s ease;
}

.phase-card:hover .phase-icon {
  transform: rotateY(360deg);
}

.phase-card h3 {
  color: #4a90e2;
  font-size: 1.3rem;
  margin: 1rem 0;
  line-height: 1.4;
}

.phase-line {
  position: absolute;
  top: 50%;
  right: -1rem;
  width: 2rem;
  height: 2px;
  background: linear-gradient(90deg, #4a90e2, transparent);
}

.phase-card:last-child .phase-line {
  display: none;
}

@media (max-width: 1200px) {
  .phase-line {
    display: none;
  }
  
  .phase-card {
    margin-bottom: 2rem;
  }
}

@media (max-width: 768px) {
  .approach-section h2 {
    font-size: 2rem;
  }

  .phase-card {
    padding: 1.5rem;
  }

  .phase-card h3 {
    font-size: 1.1rem;
  }
}

.projects-page {
  background: #121212;
  min-height: 100vh;
  color: #ffffff;
}

.project-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 2rem;
}

.project-intro {
  text-align: center;
  margin-bottom: 4rem;
}

.project-intro h2 {
  color: #4a90e2;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 1rem;
}

.project-intro h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #4a90e2, transparent);
}

.intro-text {
  font-size: 1.2rem;
  line-height: 1.8;
  max-width: 800px;
  margin: 0 auto;
  color: rgba(255, 255, 255, 0.9);
}

.key-points-section,
.examples-section,
.considerations-section {
  margin-bottom: 4rem;
}

h2 {
  color: #4a90e2;
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
  margin-top: 3rem;
  padding: 0 2rem;
}

.feature-card {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(74, 144, 226, 0.2);
  border-radius: 20px;
  padding: 2.5rem;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 300px;
  width: 100%;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, #4a90e2, #357abd);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
  border-color: rgba(74, 144, 226, 0.4);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.feature-card:hover::before {
  opacity: 1;
}

.feature-icon {
  font-size: 2.8rem;
  color: #4a90e2;
  margin-bottom: 2rem;
  transition: transform 0.3s ease;
}

.feature-card:hover .feature-icon {
  transform: scale(1.1);
}

.feature-card h3 {
  color: #ffffff;
  font-size: 1.6rem;
  margin-bottom: 1.2rem;
  font-weight: 600;
}

.feature-card p {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.8;
  font-size: 1.1rem;
  margin: 0;
}

.example-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.example-card {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  transition: all 0.3s ease;
}

.example-card:hover {
  transform: translateY(-5px);
  border-color: rgba(74, 144, 226, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.example-card h3 {
  color: #4a90e2;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.example-card p {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
}

.considerations-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.consideration-card {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  transition: all 0.3s ease;
}

.consideration-card:hover {
  transform: translateY(-5px);
  border-color: rgba(74, 144, 226, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.consideration-content h3 {
  color: #ffffff;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.consideration-content p {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
}

@media (max-width: 1200px) {
  .features-grid {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 1rem;
  }
}

@media (max-width: 768px) {
  .project-content {
    padding: 2rem 1rem;
  }

  .project-intro h2 {
    font-size: 2rem;
  }

  .intro-text {
    font-size: 1.1rem;
  }

  .feature-card,
  .example-card,
  .consideration-card {
    padding: 1.5rem;
  }

  .key-points-section {
    padding: 1.5rem;
    margin: 3rem 0;
  }

  .features-grid {
    grid-template-columns: 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  .feature-card {
    min-width: unset;
    width: 100%;
    padding: 2rem;
  }

  .feature-icon {
    font-size: 2.4rem;
    margin-bottom: 1.5rem;
  }

  .feature-card h3 {
    font-size: 1.4rem;
  }

  .feature-card p {
    font-size: 1rem;
  }

  .considerations-grid {
    grid-template-columns: 1fr;
  }
  
  .consideration-card {
    padding: 1.5rem;
  }
}

/* Add Project Image Styling */
.project-image-container {
  margin: 3rem auto;
  max-width: 1000px;
  padding: 0 2rem;
}

.project-overview-image {
  width: 100%;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.project-overview-image:hover {
  transform: scale(1.02);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .project-image-container {
    padding: 0 1rem;
    margin: 2rem auto;
  }
}

